.ng2-c-toast {
  color: #ffffff;
  display: table;
  font-size: 14px;
  left: 0;
  min-height: 50px;
  position: fixed;
  top: 0;
  transform: translateY(-100%);
  transition: transform 200ms ease-out;
  width: 100%;
  z-index: 10000; }

.ng2-c-toast--success {
  background-color: #6ec60f; }

.ng2-c-toast--warning {
  background-color: #e17d00; }

.ng2-c-toast--error {
  background-color: #d14700; }

.ng2-c-toast__right {
  align-items: center;
  bottom: 0;
  display: flex;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 3; }
  .ng2-c-toast__right .ng2-c-header__icon {
    font-size: 14px; }

.ng2-c-toast__center {
  display: table-cell;
  padding: 10px 50px;
  text-align: center;
  vertical-align: middle;
  width: 100%; }

.ng2-c-toast__title {
  font-size: inherit;
  font-weight: 400;
  margin: 0; }

.ng2-c-toast__custom,
.ng2-c-toast__clickable-container {
  align-items: center;
  display: flex;
  flex-direction: column; }
  .ng2-c-toast__custom > *,
  .ng2-c-toast__clickable-container > * {
    color: #ffffff;
    margin-bottom: 5px; }
    .ng2-c-toast__custom > *:last-child,
    .ng2-c-toast__clickable-container > *:last-child {
      margin-bottom: 0; }
  .ng2-c-toast__custom a:not(.ng2-c-toast__clickable-container),
  .ng2-c-toast__clickable-container a:not(.ng2-c-toast__clickable-container) {
    color: #ffffff;
    text-decoration: underline; }

.ng2-c-toast__clickable-container {
  text-decoration: none;
  width: 100%; }
  .ng2-c-toast__clickable-container:hover {
    color: #ffffff; }

.body--is-ios .ng2-c-toast {
  padding-top: 20px;
  padding-top: constant(safe-area-inset-top);
  padding-top: env(safe-area-inset-top); }

.body--is-ios .ng2-c-toast__right {
  top: 20px;
  top: constant(safe-area-inset-top);
  top: env(safe-area-inset-top); }
